export default {
  data () {
    return {
		loading:true,
      search_key: '',
      type: '国内',
      type_index: 1, // 1国内2国外
      typeList: ['国内', '国外'],
      picker_type: '',
      time: '全部超期VIP',
      time_index: 1,
      timeList: ['全部超期VIP', '超期0-3个月', '超期3-6个月', '超期6个月以上'],
      list: [],
      time_popup: false,
      type_popup: false,
      isFinish: false,
      page: 1,
      allLoaded: false // 分页加载
    }
  },
  onLoad (e) {
    uni.showLoading({
        title: '加载中',
    	mask:"true"
    });
  	this.type = e.type
	console.log(e)
    this.getData()
  },
  // 上拉加载更多
  onReachBottom(){
  	if(this.list.length>9){
		uni.showLoading({
		    title: '加载中',
			mask:"true"
		});
  		this.loading = true
  		this.page++
  		this.getData()
  	}
  },
  onPullDownRefresh() {
	  uni.showLoading({
	      title: '加载中',
	  	mask:"true"
	  });
  	this.page = 1
  	this.getData()
  	setTimeout(function () {
  	        uni.stopPullDownRefresh();
  			uni.showToast({
  			    title: '刷新成功',
  			    duration: 1000,
  				icon:'none'
  			});
  	  }, 2000);
  
  },
  
  methods: {
	  tel(tel){
		  uni.makePhoneCall({
		  	phoneNumber:tel
		  })
	  },
    getData () {
      this.$api.other_renew({
        search: this.search_key,
        type: this.type,
        time: this.time_index,
        page: this.page
      }).then(res => {
        uni.hideLoading();
        if (res.data.code === '200') {
			if (this.page === 1) {
				  this.list = res.data.data.list
				  // console.log(this.chinaList)
				  this.loading = false
				  this.isFinish = true
			} else {
			  if(res.data.data.list.length){
				  setTimeout(()=>{
					  this.list = this.list.concat(res.data.data.list)
					  this.loading = false
					  uni.showToast({
						title:'加载成功',
						icon:"none"
					  })
				  },1500)
			  }else{
				  this.loading = false
				  uni.showToast({
					title:'没有更多的数据了',
					icon:"none"	,								  	
				  })
			  }	
			   this.isFinish = true					  
			}
          
        }
      })
    },
    search () {
      if (this.search_key.length) {
        uni.showLoading({
              title: '搜索中',
          	mask:"true"
          });
        }
		this.page = 1
        this.getData()
    },
	// 点击选中时间范围
	bindPickerChange2(e){
		uni.showLoading({
		    title: '加载中',
			mask:"true"
		});
		this.time = this.timeList[e.detail.value]
		if (this.time === '全部超期VIP') {
		  this.time_index = 1
		} else if (this.time === '超期0-3个月') {
		  this.time_index = 2
		} else if (this.time === '超期3-6个月') {
		  this.time_index = 3
		} else if (this.time === '超期6个月以上') {
		  this.time_index = 4
		}
		this.page = 1
		this.isFinish = false
		this.list = []
		this.getData()
	},
    
    my_info(item){
		// console.log(item)
		uni.navigateTo({
			url:'../../pagesMy/MyInfo/MyInfo?id='+item.user_id
		})
    }
  }
}
